.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  margin:2px;
}

img {
  max-width:80vw;
max-height:90vh;
margin:0 auto;

}

button {
  position:fixed;
  top:8px;
  left:8px;

  cursor:pointer;
  border:1px solid salmon;
  background:salmon;
  font-family:inherit;
  font-weight:bold;
  color:white;
  /* padding:2%; */
  font-size:40px;
  text-transform: uppercase;
  transition:all 100ms;
}
button:hover {
  color:rgb(252, 159, 149);
}